import { request } from '../client'

export async function forgetMyPassword (email) {
  try {
    await request({
      method: 'POST',
      url: '/users/forget-my-password',
      data: { email }
    })

    return true
  } catch (e) {
    throw new Error(e.response.data.errors[0].detail)
  }
}

export async function resetPassword (token, password) {
  try {
    await request({
      method: 'POST',
      url: '/users/reset-password/' + token,
      data: { password }
    })

    return true
  } catch (e) {
    throw new Error(e.response.data.errors[0].detail)
  }
}
