import React, { useState } from 'react'
import PageCover from '../page-cover'
import { forgetMyPassword } from '../../../core/services/user-service'

export default function LoginPage () {
  const [email, setEmail] = useState('')
  const [loading, setLoading] = useState(false)
  const [errorMessage, setErrorMessage] = useState(null)
  const [successMessage, setSuccessMessage] = useState(null)

  async function handleSubmit (ev) {
    ev.preventDefault()
    setLoading(true)
    setErrorMessage(null)

    try {
      await forgetMyPassword(email)
      setSuccessMessage('Check your e-mail for instructions to reset your password')
    } catch (e) {
      setErrorMessage(e.message)
    } finally {
      setLoading(false)
    }
  }

  return (
    <React.Fragment>
      <PageCover title="Forget my password" />

      <section className="probootstrap-section">
        <div className="container">
          <h2 className="mb-5">Forget my password</h2>
          {successMessage ? (
            <div class="alert alert-success">
              {successMessage}
            </div>
          ) : (
            <form onSubmit={handleSubmit}>
              <div className="row">
                <div className="col-md-6">
                  <div className="form-group">
                    <label htmlFor="email">Email</label>
                    <input id="email" className="form-control" type="email" value={email} onChange={e => setEmail(e.target.value)} />
                  </div>
                  {errorMessage &&
                    <div className="alert alert-danger">
                      {errorMessage}
                    </div>
                  }
                  <div className="form-group">
                    <input className="btn btn-primary" type="submit" value={loading ? 'Wait' : 'Send email'} disabled={loading} />
                  </div>
                </div>
              </div>
            </form>
          )}
        </div>
      </section>
    </React.Fragment>
  )
}
